var beep_sound = new Audio(
    'data:audio/mpeg;base64,'
);

const browser = window.browser;

export const create_tab = async ({ active = false, url, task_id, cookies, account_email, proxy, onResolved }) => {
    beep_sound.play();

    const identities = (await browser.contextualIdentities.query({})) ?? [];

    let identity_found = false;

    for (const identity of identities) {
        const identity_split = identity.name.split('-');

        const identity_name = identity_split[0];
        const identity_proxy = identity_split[1] ?? '';
        const identity_task_id = identity_split[2] ?? '';

        if (identity_name === account_email && identity_task_id == task_id) {
            identity_found = identity;

            if (identity_proxy !== proxy) {
                const updated_identity = await browser.contextualIdentities.update(identity.cookieStoreId, {
                    name: `${account_email}-${proxy}-${task_id}`,
                });
                identity_found = updated_identity;
            }

            break;
        }
    }

    if (!identity_found) {
        identity_found = await browser.contextualIdentities.create({
            name: `${account_email}-${proxy}-${task_id}`,
            color: 'green',
            icon: 'dollar',
        });
    }

    for (let cookie of cookies) {
        await browser.cookies.set({
            url: 'https://account.appen.com',
            expirationDate: cookie.maxAge,
            name: cookie.key,
            secure: cookie.secure,
            value: cookie.value,
            httpOnly: cookie.httpOnly,
            storeId: identity_found.cookieStoreId,
        });
    }

    const tab = await browser.tabs.create({
        active,
        url,
        cookieStoreId: identity_found.cookieStoreId,
    });

    const listener = async function (tabId) {
        if (tabId === tab.id) {
            await onResolved();
            browser.tabs.onRemoved.removeListener(listener);
        }
    };

    browser.tabs.onRemoved.addListener(listener);
};
